import React from 'react';
import './Maze.css';
import playerFig from './img/player.png'
import finishFlag from './img/finish-flag.png'

export const Maze = (props) => {
    return (
        <div className="Labyrinth">
            {
                props.dimension.map((row, y) => {
                    return row.map((elem, x) => {
                        let cell = props.dimension[y][x];
                        let playerPos = props.dimension[props.player.posX][props.player.posY];
                        let finishPos = props.dimension[4][4];

                        let player = <img src={playerFig} className='Player' alt='player-img' />;

                        let fieldClasses = "Labyrinth__field ";

                        if (cell.top) {
                            fieldClasses += "Wall-top ";
                        }
                        if (cell.bottom) {
                            fieldClasses += "Wall-bottom ";
                        }
                        if (cell.left) {
                            fieldClasses += "Wall-left ";
                        }
                        if (cell.right) {
                            fieldClasses += "Wall-right";
                        }

                        if (cell === playerPos) {
                            return <div className={fieldClasses} id="Player">
                                       {player}
                                   </div>
                        } else if (cell === finishPos) {
                            return <div className={fieldClasses} id="Finish">
                                       <img src={finishFlag} alt='finish-flag-img' />
                                   </div>
                        } else {
                            return <div className={fieldClasses}/>
                        }
                    })
                })
            }
        </div>
    );
};


